import React from 'react'
import { PageProps } from 'gatsby'

import { Column, Text } from '@flame-ui/react'
import { Logo } from '../components/main/Logo'
import { Button } from '../components/ui'

import { useSiteMetadata } from '../hooks/useSiteMetadata'

const Home: React.FC<PageProps> = () => {
  const { title, description } = useSiteMetadata()
  return (
    <main>
      <Column
        justifyContent="center"
        alignItems="center"
        gap={3}
        height="100vh"
        bg="primary"
        color="white"
      >
        <Logo size={180} />
        <Text fontSize="h1" fontWeight={700}>
          {title}
        </Text>
        <Text fontSize="body" width="290px" textAlign="center">
          {description}
        </Text>
        <Button to="/get-started/install">Get started</Button>
      </Column>
    </main>
  )
}

export default Home
