import React from 'react'
import { Link } from 'gatsby'

import { Box } from '@flame-ui/react'

export const Button: React.FC<{ to?: string }> = ({ to, children }) => {
  if (to) {
    return (
      <Link to={to}>
        <Box px={[3, 4]}>{children}</Box>
      </Link>
    )
  } else {
    return (
      <button>
        <Box px={[3, 4]}>{children}</Box>
      </button>
    )
  }
}
